import React from "react";
import { Grid, Container, Box } from "@material-ui/core";
import LooksTwoIcon from "@material-ui/icons/LooksTwo";
import LooksOneIcon from "@material-ui/icons/LooksOne";
import Looks3Icon from "@material-ui/icons/Looks3";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles(({ palette, ...theme }) => ({
  icon: {
    marginBottom: theme.spacing(1),
  },

  p: {
    fontFamily: "Roboto",
    fontStyle: "normal",
    fontWeight: "500",
    fontSize: "1em",
    lineHeight: "19px",
    textAlign: "justify",
  },
}));

const FourSection = () => {
  const classes = useStyles();

  return (
    <section className="section">
      <Container maxWidth="lg">
        <Box py={6}>
          <Grid container spacing={4}>
            <Grid item xs={12} md={4}>
              <Box textAlign="center">
                <LooksOneIcon
                  color="primary"
                  fontSize="large"
                  className={classes.icon}
                />
              </Box>

              <Box textAlign="left">
                <p className={classes.p}>
                  La premire sance est <strong>verbale</strong>.<br></br>
                  <br></br>
                  en dbut de sance vous faites le point sur le vcu et les
                  tensions corporelles.
                </p>
              </Box>
            </Grid>
            <Grid item xs={12} md={4}>
              <Box textAlign="center">
                <LooksTwoIcon
                  color="primary"
                  fontSize="large"
                  className={classes.icon}
                />
              </Box>

              <Box textAlign="left">
                <p className={classes.p}>
                  La deuxieme est concentre sur le <strong>soin</strong>.
                  <br></br>
                  <br></br>
                  Cette phase intgre lutilisation de diffrentes techniques de
                  toucher-massages en fonction de chaque personne, pouvant
                  utiliser le massage californien, shiatsu, nergtique,
                  relaxation, rflexologie plantaire mais aussi fait appel aux
                  sons et aux vibrations avec la sonothrapie (Bols tibtains et
                  autres instruments musicaux).
                  <br></br>
                  <br></br>
                  Ces techniques de relaxation ancestrale dnouent les tensions
                  et rtablissent le bon fonctionnement des organes internes, en
                  rquilibrant vos nergies.
                  <br></br>
                  <br></br>
                  La consquence de ce soin engendre un autre ressenti de
                  vous-mme.
                </p>
              </Box>
            </Grid>

            <Grid item xs={12} md={4}>
              <Box textAlign="center">
                <Looks3Icon
                  color="primary"
                  fontSize="large"
                  className={classes.icon}
                />
              </Box>

              <Box textAlign="left">
                <p className={classes.p}>
                  La troisime phase <strong> dite dintgration</strong>:
                  <br></br>
                  <br></br>
                  cest la partie la plus importante ralise entre vous et
                  laccompagnateur. Tout en tant aid vous exprimez vos
                  ressentis et vos penses. En clarifiant celles-ci grce  la
                  relaxation vous pourrez envisager de dpasser vos difficults.
                  <br></br>
                  <br></br>
                  Vous pourrez ainsi vous baser sur chaque sance pour induire
                  le changement en vous et  trouver vos propres solutions.
                </p>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </Container>
    </section>
  );
};

export default FourSection;
