import React from "react";
import {
  Grid,
  Container,
  Box,
  Card,
  Typography,
  CardContent,
  CardActionArea,
} from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles(({ palette, ...theme }) => ({
  title: {
    fontFamily: "Lato",
    width: "100%",
    margin: "auto",
  },

  img: {
    maxHeight: "6em",
    margin: "auto",
    marginBottom: theme.spacing(2),
    marginTop: theme.spacing(4),
  },

  p: {
    fontFamily: "Roboto",
    fontStyle: "normal",
    fontWeight: "600",
    fontSize: "1em",
    lineHeight: "30px",
    textAlign: "left",
  },
}));

const FourthSection = () => {
  const classes = useStyles();

  const imgList = [
    {
      title: "Equilibre",
      link: "/assets/images/formations/energie.png",
      description:
        "Un employ dtendu est plus nergique, plus concentr.Il offre une meilleure performance.Il gagne en motivation, en crativit, bref en efficacit et en savoir tre.",
    },

    {
      title: "Teamwork",
      link: "/assets/images/formations/environement.png",
      description:
        "En agissant sur notre propre quilibre, le massage-bien-tre renforce galement le sentiment dappartenance et amliore le climat social, offrant un environnement qui favorise l'panouissement le dveloppement des performances.",
    },

    {
      title: "Entreprise",
      link: "/assets/images/formations/work.png",
      description:
        "Pour toute activit professionnelle, un employ dtendu est un employ motiv et cratif.Il est mieux concentr et sait bien mieux grer les situations stressantes. Un environnement de travail zen est important pour la sant de chaque entreprise.",
    },
  ];

  const getImgSrc = (id, imgSrc) => {
    return <img src={imgSrc} alt="" className={classes.img} />;
  };

  return (
    <section className="section">
      <Container maxWidth="lg">
        <Box textAlign="center">
          <Grid container justify="space-between" alignItems="center">
            {imgList.map((item) => (
              <Grid item md={3} xs={12}>
                <Card className={classes.root}>
                  <CardActionArea>
                    {getImgSrc(item.id, item.link)}
                    <CardContent>
                      <Typography gutterBottom variant="body2" component="h6">
                        {item.title}
                      </Typography>

                      <Box align="left">
                        <Typography
                          variant="p"
                          color="textSecondary"
                          component="p"
                        >
                          {item.description}
                        </Typography>
                      </Box>
                    </CardContent>
                  </CardActionArea>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Box>
      </Container>
    </section>
  );
};

export default FourthSection;
